@import "../common/styles/sass/variables.scss";
@import "../common/styles/sass/mixins";

.contactsBlock {
    background-color: $bgColor;
    padding: 100px 0;
    @media screen and (max-width: 750px){
        padding: 80px 0;
    }
    .contactsContainer {
        @include flexable(flex-start, center, column);
        @include container();
        .formContainer {
            @include flexable(flex-start, center, column);
            width: 50%;
            .input {
                margin-top: 45px;
                margin-bottom: 10px;
                background-color: $bgSecondColor;
                border: 1px solid #4a4a4a;
                border-radius: 0;
                padding: 0 15px;
                @include text(('Poppins', sans-serif), 14px, 1.2);
                height: calc(1.5em + 0.75rem + 2px);
            }
            .input2 {
                margin-bottom: 10px;
                background-color: $bgSecondColor;
                border: 1px solid #4a4a4a;
                border-radius: 0;
                padding: 0 15px;
                @include text(('Poppins', sans-serif), 14px, 1.2);
                height: calc(1.5em + 0.75rem + 2px);
            }
            .textarea {
                background-color: $bgSecondColor;
                border: 1px solid #4a4a4a;
                border-radius: 0;
                padding: 15px 15px;
                @include text(('Poppins', sans-serif), 14px, 1.2);
            }
            .button {
                @include text(('Poppins', sans-serif), 14px, 1.2);
                height: 35px;
                width: 160px;
                background-color: $primaryColor;
                border: none;
                margin-top: 15px;
                cursor: pointer;
                &:hover {
                    background-color: #2771ff;
                }
            }
        }
        .formContainer input, .formContainer textarea {
            width: 70%;
            @media screen and (max-width: 1000px){
                width: 80%;
            }
            @media screen and (max-width: 800px){
                width: 90%;
            }
            @media screen and (max-width: 700px){
                width: 100%;
            }
            @media screen and (max-width: 600px){
                width: 125%;
            }
            @media screen and (max-width: 500px){
                width: 150%;
            }
            @media screen and (max-width: 400px){
                width: 170%;
            }
        }
    }
}


